export const setChartOptions = () => {
  let options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
        labels: {
          usePointStyle: true,
          font: {
            size: 14,
            weight: 700,
          },
        },
      },
      tooltip: {
        usePointStyle: true,
        callbacks: {
          label: function (context) {
            let label = context.dataset.label || '';
            if (label) {
              label += ' : ';
            }
            if (context.parsed.y !== null) {
              label += context.parsed.y + ' %';
            }
            return label;
          },
        },
      },
    },
    scales: {
      x: {
        grid: {
          display: false,
        },
      },
      y: {
        grid: {
          drawBorder: false,
          tickColor: 'transparent',
        },
        ticks: {
          stepSize: 25,
          callback: function (val, index) {
            return val + ' %';
          },
        },
      },
    },
  };
  return options;
};

export const createData = (labels, PM10, PM2, SO2, NO2, CO, O3) => {
  let data = {
    labels,
    datasets: [
      {
        label: 'PM\u2081\u2080',
        data: PM10,
        tension: 0.3,
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
        pointStyle: 'circle',
        pointRadius: 4,
      },
      {
        label: 'PM\u2082.\u2085',
        data: PM2,
        tension: 0.3,
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
        pointStyle: 'rect',
        pointRadius: 4,
      },
      {
        label: 'SO\u2082',
        data: SO2,
        tension: 0.3,
        borderColor: 'rgb(141, 235, 53)',
        backgroundColor: 'rgba(126, 235, 53, 0.5)',
        pointStyle: 'triangle',
        pointRadius: 4,
      },
      {
        label: 'NO\u2082',
        data: NO2,
        tension: 0.3,
        borderColor: 'rgb(255, 252, 99)',
        backgroundColor: 'rgba(255, 239, 99, 0.5)',
        pointStyle: 'rectRot',
        pointRadius: 4,
      },
      {
        label: 'CO',
        data: CO,
        tension: 0.3,
        borderColor: 'rgb(235, 53, 226)',
        backgroundColor: 'rgba(177, 53, 235, 0.5)',
        pointStyle: 'rectRounded',
        pointRadius: 4,
      },
      {
        label: 'O\u2083',
        data: O3,
        tension: 0.3,
        borderColor: 'rgb(53, 235, 159)',
        backgroundColor: 'rgba(53, 235, 190, 0.5)',
        pointStyle: 'star',
        pointRadius: 4,
      },
    ],
  };
  return data;
};
