import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.nav`
  position: relative;
  overflow: hidden;
  .select {
    position: relative;
    z-index: 2;
    .select__inner {
      select {
        width: 94px;
        padding: 5px 0 5px 5px;
        border: 1px solid #ccc;
        border-radius: 0.25rem;
        font-size: 14px;
        background-color: #ffffffb3;
      }
      .pin-btn {
        position: absolute;
        top: 0;
        right: -85px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70px;
        height: 30px;
        border: 1px solid #eee;
        cursor: pointer;
      }
      .pin {
        position: absolute;
        top: 0;
        right: -30px;
        display: none;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        cursor: pointer;
        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  @media (max-width: 479.98px) {
    select {
      width: 80px !important;
    }
    .pin-btn {
      display: none !important;
    }
    .pin {
      display: flex !important;
    }
  }
  @media (max-width: 374.98px) {
    label {
      display: flex;
      flex-direction: column;
    }
    .pin {
      right: -24px !important;
    }
  }
`;

const Nav = ({
  selectedCounty,
  selectedSite,
  options,
  changeCounty,
  changeSite,
  color,
  handleClick,
}) => {
  return (
    <Wrapper>
      <div className="container-xl position-relative">
        <div className="d-flex justify-content-center p-3 select">
          <div className="position-relative d-flex justify-content-center select__inner">
            <label className="me-2">
              <span> : </span>
              <select
                value={selectedCounty}
                id="county"
                onChange={(e) => {
                  changeCounty(e);
                }}
              >
                <option value="Keelung"></option>
                <option value="Taipei"></option>
                <option value="Newtaipei"></option>
                <option value="Taoyuan"></option>
                <option value="Hsinchu_city"></option>
                <option value="Hsinchu"></option>
                <option value="Miaoli"></option>
                <option value="Taichung"></option>
                <option value="Nantou"></option>
                <option value="Changhua"></option>
                <option value="Yunlin"></option>
                <option value="Chiayi_city"></option>
                <option value="Chiayi"></option>
                <option value="Tainan"></option>
                <option value="Kaohsiung"></option>
                <option value="Pingtung"></option>
                <option value="Yilan"></option>
                <option value="Hualien"></option>
                <option value="Taitung"></option>
                <option value="Penghu"></option>
                <option value="Kinmen"></option>
                <option value="Lienchiang"></option>
              </select>
            </label>
            <label>
              <span> : </span>
              <select
                value={selectedSite}
                id="site"
                onChange={(e) => {
                  changeSite(e);
                }}
              >
                {options}
              </select>
            </label>
            <button
              type="button"
              className="btn btn-light mx-2 ps-2 pe-1 pin-btn"
              title=""
              onClick={handleClick}
            >
              <span></span>
              <svg
                fill="none"
                height="20"
                viewBox="0 0 20 20"
                width="20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M13.3251 2.61804C12.3599 1.65275 10.7326 1.91683 10.1221 3.13783L8.39285 6.59628C8.22855 6.92486 7.94939 7.18173 7.60829 7.31817L4.01834 8.75415C3.35177 9.02078 3.17498 9.88209 3.68262 10.3897L6.29289 13L3 16.2929V17H3.70711L7 13.7071L9.61027 16.3174C10.1179 16.825 10.9792 16.6482 11.2459 15.9817L12.6818 12.3917C12.8183 12.0506 13.0751 11.7715 13.4037 11.6072L16.8622 9.87793C18.0832 9.26743 18.3472 7.64015 17.382 6.67486L13.3251 2.61804Z"
                  fill={color}
                />
              </svg>
            </button>
            <div className="pin" title="" onClick={handleClick}>
              <svg
                fill="none"
                height="20"
                viewBox="0 0 20 20"
                width="20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M13.3251 2.61804C12.3599 1.65275 10.7326 1.91683 10.1221 3.13783L8.39285 6.59628C8.22855 6.92486 7.94939 7.18173 7.60829 7.31817L4.01834 8.75415C3.35177 9.02078 3.17498 9.88209 3.68262 10.3897L6.29289 13L3 16.2929V17H3.70711L7 13.7071L9.61027 16.3174C10.1179 16.825 10.9792 16.6482 11.2459 15.9817L12.6818 12.3917C12.8183 12.0506 13.0751 11.7715 13.4037 11.6072L16.8622 9.87793C18.0832 9.26743 18.3472 7.64015 17.382 6.67486L13.3251 2.61804Z"
                  fill={color}
                />
              </svg>
            </div>
          </div>
        </div>
      </div>
    </Wrapper>
  );
};

export default Nav;
