import React from 'react';
import styled from 'styled-components';
import { setChartOptions, createData } from '../js/createChart';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const Wrapper = styled.div`
  width: 100%;
  background-color: #e1f1ff;
  .pb-75 {
    padding-bottom: 75px;
  }
  .chart {
    width: 100%;
    .chart__title {
      width: 100%;
      font-size: 24px;
      font-weight: 700;
    }
    .chart__inner {
      width: 100%;
    }
    .h70 {
      height: 70vh;
    }
  }
  .f-14 {
    font-size: 14px;
  }
  .text {
    height: 150px;
    line-height: 150px;
    color: #3f3f3f;
  }
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #e1f1ffe2;
  }
  @media (max-width: 424.98px) {
    .chart .chart__title {
      font-size: 20px !important;
    }
  }
`;

function Chart({ PM10, PM2, SO2, NO2, CO, O3, Time, SITENAME, SiteName }) {
  let options = setChartOptions();
  let data = createData(Time, PM10, PM2, SO2, NO2, CO, O3);

  return (
    <Wrapper className="d-flex justify-content-center align-items-center position-relative px-3 px-sm-5">
      <div className={PM10 && PM10.length > 1 ? 'chart pb-75' : 'chart pb-4'}>
        <div className="chart__title text-center pt-5 pb-4">
          <img className="me-2" src="./img/chart-icon.png" alt="" />
          {SiteName === '' ||
          SiteName === '' ||
          SiteName === '' ||
          SiteName === ''
            ? SiteName + '10'
            : SiteName === '()' ||
              SiteName === '()' ||
              SiteName === '()' ||
              SiteName === '()' ||
              SiteName === '()' ||
              SiteName === '()'
            ? ''
            : SiteName
            ? SiteName + ''
            : ''}
        </div>
        <div
          className={
            PM10 && PM10.length > 1
              ? 'p-3 bg-white chart__inner '
              : 'chart__inner'
          }
        >
          {PM10 && PM10.length > 1 ? (
            <Line
              className="h70"
              datasetIdKey="id"
              options={options}
              data={data}
            />
          ) : (
            ''
          )}
        </div>
      </div>
    </Wrapper>
  );
}

export default Chart;
