export const changeChartData = (arrayOfData, index, property, latestArray) => {
  return (
    arrayOfData && arrayOfData[index][property] !== latestArray[index][property]
  );
};

export const changeSiteData = (obj, latestObj) => {
  return (
    obj &&
    (obj.SiteName !== latestObj.SiteName ||
      obj.ImportDate !== latestObj.ImportDate)
  );
};
