import stations from './stations.js';

const createDefaultSettingOptions = (optionsObj) => {
  return (optionsObj = Object.assign(
    {
      county: 'Taipei',
      site: '',
      pinColor: '#8d8d8d',
    },
    optionsObj
  ));
};

const getLocalStorageItem = (item) => {
  let localStorageItem = localStorage.getItem(item);
  if (localStorageItem) {
    return parseItem(localStorageItem);
  }
  return null;
};

const parseItem = (item) => {
  try {
    if (JSON.parse(item)) {
      return checkObjectKeyValue(JSON.parse(item));
    } else {
      throw new Error('Parse Error');
    }
  } catch (e) {
    console.log(e);
  }
};
const checkObjectKeyValue = (obj) => {
  let county = obj['county'];
  let site = obj['site'];
  let sitesInCountyArray = stations[county];
  let isSiteNameCorrect = checkSiteName(site, sitesInCountyArray);

  if (!county || !site || !sitesInCountyArray || !isSiteNameCorrect) {
    return null;
  }

  return obj;
};

const checkSiteName = (siteName, arrayOfObjects) => {
  let isSiteNameCorrect = false;

  arrayOfObjects.forEach((item) => {
    let text = Object.keys(item).join();
    if (text === siteName) {
      isSiteNameCorrect = true;
    }
  });

  return isSiteNameCorrect;
};

const selectedColor = (color = '#ff3153') => {
  return color;
};

const isUserSettingOptions = (optionsObj, defaultOptionsObj) => {
  if (optionsObj) {
    let color = selectedColor();
    optionsObj['pinColor'] = color;
    return changeDefaultSetting(optionsObj, defaultOptionsObj);
  }
  return null;
};

const changeDefaultSetting = (newSettingObj, defaultSettingObj) => {
  for (let key in defaultSettingObj) {
    defaultSettingObj[key] = newSettingObj[key];
  }
  return defaultSettingObj;
};

export const initialSettingOptions = () => {
  let defaultOptions = createDefaultSettingOptions();
  let userSettingOptions = getLocalStorageItem('site');
  return (
    isUserSettingOptions(userSettingOptions, defaultOptions) || defaultOptions
  );
};

export const createOptionElements = (arrayOfObjects) => {
  return arrayOfObjects.map((obj) => {
    let text = Object.keys(obj).join();
    return (
      <option key={text} value={text}>
        {text}
      </option>
    );
  });
};
