import React, { useState, useEffect } from 'react';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from '../conponents/Header';
import Nav from '../conponents/Nav';
import Footer from '../conponents/Footer';
import Main from '../conponents/Main';
import Chart from '../conponents/Chart';
import stations from '../js/stations';
import { initialSettingOptions, createOptionElements } from '../js/options.js';
import { changeChartData, changeSiteData } from '../js/changeData.js';
// Initialize Cloud Firestore through Firebase
import { initializeApp } from 'firebase/app';
import { getFirestore, doc, onSnapshot } from 'firebase/firestore';
import config from '../config/config';
const firebaseApp = initializeApp(config);
const db = getFirestore();

function Home() {
  let { county, site, pinColor } = initialSettingOptions();
  let sitesInCountyArray = stations[county];
  let optionElements = createOptionElements(sitesInCountyArray);

  const [selectedCounty, setSelectedCounty] = useState(county);
  const [selectedSite, setSelectedSite] = useState(site);
  const [options, setOptions] = useState(optionElements);
  const [color, setColor] = useState(pinColor);
  const [chartAQI, setChartAQI] = useState(null);
  const [chartPM10, setChartPM10] = useState(null);
  const [chartPM25, setChartPM25] = useState(null);
  const [chartSO2, setChartSO2] = useState(null);
  const [chartNO2, setChartNO2] = useState(null);
  const [chartCO, setChartCO] = useState(null);
  const [chartO3, setChartO3] = useState(null);
  const [chartTime, setChartTime] = useState(null);
  const [chartSITENAME, setChartSITENAME] = useState(null);
  const [SiteObj, setSiteObj] = useState(null);
  const [SiteName, setSiteName] = useState(null);
  const [County, setCounty] = useState(null);
  const [AQI, setAQI] = useState(null);
  const [Status, setStatus] = useState(null);
  const [SO2, setSO2] = useState(null);
  const [CO, setCO] = useState(null);
  const [CO_8hr, setCO_8hr] = useState(null);
  const [O3, setO3] = useState(null);
  const [O3_8hr, setO3_8hr] = useState(null);
  const [PM10, setPM10] = useState(null);
  const [PM10_AVG, setPM10_AVG] = useState(null);
  const [PM2, setPM2] = useState(null);
  const [PM2_AVG, setPM2_AVG] = useState(null);
  const [NO2, setNO2] = useState(null);
  const [PublishTime, setPublishTime] = useState(null);

  const changeCounty = (e) => {
    let county = e.target.value;
    setSelectedCounty(county);

    sitesInCountyArray = stations[county];
    optionElements = createOptionElements(sitesInCountyArray);
    setOptions(optionElements);

    let defaultSite = Object.keys(sitesInCountyArray[0]).join();
    setSelectedSite(defaultSite);
    setColor('#8d8d8d');

    localStorage.removeItem('site');
  };
  const changeSite = (e) => {
    let site = e.target.value;
    setSelectedSite(site);
    setColor('#8d8d8d');

    localStorage.removeItem('site');
  };
  const handleClick = () => {
    let obj = { county: selectedCounty, site: selectedSite };
    if (localStorage.getItem('site')) {
      localStorage.removeItem('site');
      setColor('#8d8d8d');
    } else {
      localStorage.setItem('site', JSON.stringify(obj));
      setColor('#ff3153');
    }
  };

  useEffect(() => {
    const unsub = onSnapshot(doc(db, 'air', 'aqi'), (doc) => {
      let obj = doc.data();
      let arrayOfAQIData = obj[selectedSite];
      let changeSiteName = changeChartData(
        chartAQI,
        7,
        'SITENAME',
        arrayOfAQIData
      );

      if (!chartAQI || changeSiteName) {
        setChartAQI(arrayOfAQIData);
      }
    });
    return () => {
      unsub();
    };
  }, [chartAQI, selectedSite]);
  useEffect(() => {
    if (!chartPM10) {
      if (chartAQI) {
        setChartPM10(chartAQI[0]['PM10']);
      }
    } else {
      if (chartPM10 !== chartAQI[0]['PM10']) {
        setChartPM10(chartAQI[0]['PM10']);
      }
    }

    if (!chartPM25) {
      if (chartAQI) {
        setChartPM25(chartAQI[1]['PM25']);
      }
    } else {
      if (chartPM25 !== chartAQI[1]['PM25']) {
        setChartPM25(chartAQI[1]['PM25']);
      }
    }

    if (!chartSO2) {
      if (chartAQI) {
        setChartSO2(chartAQI[2]['SO2']);
      }
    } else {
      if (chartSO2 !== chartAQI[2]['SO2']) {
        setChartSO2(chartAQI[2]['SO2']);
      }
    }

    if (!chartNO2) {
      if (chartAQI) {
        setChartNO2(chartAQI[3]['NO2']);
      }
    } else {
      if (chartNO2 !== chartAQI[3]['NO2']) {
        setChartNO2(chartAQI[3]['NO2']);
      }
    }

    if (!chartCO) {
      if (chartAQI) {
        setChartCO(chartAQI[4]['CO']);
      }
    } else {
      if (chartCO !== chartAQI[4]['CO']) {
        setChartCO(chartAQI[4]['CO']);
      }
    }

    if (!chartO3) {
      if (chartAQI) {
        setChartO3(chartAQI[5]['O3']);
      }
    } else {
      if (chartO3 !== chartAQI[5]['O3']) {
        setChartO3(chartAQI[5]['O3']);
      }
    }

    if (!chartTime) {
      if (chartAQI) {
        setChartTime(chartAQI[6]['Time']);
      }
    } else {
      if (chartTime !== chartAQI[6]['Time']) {
        setChartTime(chartAQI[6]['Time']);
      }
    }

    if (!chartSITENAME) {
      if (chartAQI) {
        setChartSITENAME(chartAQI[7]['SITENAME']);
      }
    } else {
      if (chartSITENAME !== chartAQI[7]['SITENAME']) {
        setChartSITENAME(chartAQI[7]['SITENAME']);
      }
    }
  }, [
    chartAQI,
    chartPM10,
    chartPM25,
    chartSO2,
    chartNO2,
    chartCO,
    chartO3,
    chartTime,
    chartSITENAME,
  ]);
  useEffect(() => {
    const unsub = onSnapshot(doc(db, 'air', 'site'), (doc) => {
      let obj = doc.data();
      let siteAQIData = obj[selectedSite];
      let changeSiteNameOrDate = changeSiteData(SiteObj, siteAQIData);

      if (!SiteObj || changeSiteNameOrDate) {
        setSiteObj(siteAQIData);
      }
    });
    return () => {
      unsub();
    };
  }, [SiteObj, selectedSite]);
  useEffect(() => {
    if (!AQI) {
      if (SiteObj) {
        setAQI(SiteObj.AQI);
      }
    } else {
      if (AQI !== SiteObj.AQI) {
        setAQI(SiteObj.AQI);
      }
    }

    if (!SiteName) {
      if (SiteObj) {
        setSiteName(SiteObj.SiteName);
      }
    } else {
      if (SiteName !== SiteObj.SiteName) {
        setSiteName(SiteObj.SiteName);
      }
    }

    if (!County) {
      if (SiteObj) {
        setCounty(SiteObj.County);
      }
    } else {
      if (County !== SiteObj.County) {
        setCounty(SiteObj.County);
      }
    }

    if (!Status) {
      if (SiteObj) {
        setStatus(SiteObj.Status);
      }
    } else {
      if (Status !== SiteObj.Status) {
        setStatus(SiteObj.Status);
      }
    }

    if (!SO2) {
      if (SiteObj) {
        setSO2(SiteObj.SO2);
      }
    } else {
      if (SO2 !== SiteObj.SO2) {
        setSO2(SiteObj.SO2);
      }
    }

    if (!CO) {
      if (SiteObj) {
        setCO(SiteObj.CO);
      }
    } else {
      if (CO !== SiteObj.CO) {
        setCO(SiteObj.CO);
      }
    }

    if (!CO_8hr) {
      if (SiteObj) {
        setCO_8hr(SiteObj.CO_8hr);
      }
    } else {
      if (CO_8hr !== SiteObj.CO_8hr) {
        setCO_8hr(SiteObj.CO_8hr);
      }
    }

    if (!O3) {
      if (SiteObj) {
        setO3(SiteObj.O3);
      }
    } else {
      if (O3 !== SiteObj.O3) {
        setO3(SiteObj.O3);
      }
    }

    if (!O3_8hr) {
      if (SiteObj) {
        setO3_8hr(SiteObj.O3_8hr);
      }
    } else {
      if (O3_8hr !== SiteObj.O3_8hr) {
        setO3_8hr(SiteObj.O3_8hr);
      }
    }

    if (!PM10) {
      if (SiteObj) {
        setPM10(SiteObj.PM10);
      }
    } else {
      if (PM10 !== SiteObj.PM10) {
        setPM10(SiteObj.PM10);
      }
    }

    if (!PM10_AVG) {
      if (SiteObj) {
        setPM10_AVG(SiteObj['PM10_AVG']);
      }
    } else {
      if (PM10_AVG !== SiteObj['PM10_AVG']) {
        setPM10_AVG(SiteObj['PM10_AVG']);
      }
    }

    if (!PM2) {
      if (SiteObj) {
        setPM2(SiteObj['PM2.5']);
      }
    } else {
      if (PM2 !== SiteObj['PM2.5']) {
        setPM2(SiteObj['PM2.5']);
      }
    }

    if (!PM2_AVG) {
      if (SiteObj) {
        setPM2_AVG(SiteObj['PM2.5_AVG']);
      }
    } else {
      if (PM2_AVG !== SiteObj['PM2.5_AVG']) {
        setPM2_AVG(SiteObj['PM2.5_AVG']);
      }
    }

    if (!NO2) {
      if (SiteObj) {
        setNO2(SiteObj['NO2']);
      }
    } else {
      if (NO2 !== SiteObj['NO2']) {
        setNO2(SiteObj['NO2']);
      }
    }

    if (!PublishTime) {
      if (SiteObj) {
        setPublishTime(SiteObj['PublishTime']);
      }
    } else {
      if (PublishTime !== SiteObj['PublishTime']) {
        setPublishTime(SiteObj['PublishTime']);
      }
    }
  }, [
    SiteObj,
    AQI,
    SiteName,
    County,
    Status,
    SO2,
    CO,
    CO_8hr,
    O3,
    O3_8hr,
    PM10,
    PM10_AVG,
    PM2,
    PM2_AVG,
    NO2,
    PublishTime,
  ]);

  return (
    <React.Fragment>
      <Header>
        <Nav
          selectedCounty={selectedCounty}
          selectedSite={selectedSite}
          options={options}
          changeCounty={(e) => changeCounty(e)}
          changeSite={(e) => changeSite(e)}
          color={color}
          handleClick={handleClick}
        />
      </Header>
      <Main
        PublishTime={PublishTime}
        County={County}
        SiteName={SiteName}
        AQI={AQI}
        PM2_AVG={PM2_AVG}
        PM2={PM2}
        PM10_AVG={PM10_AVG}
        PM10={PM10}
        O3_8hr={O3_8hr}
        O3={O3}
        CO_8hr={CO_8hr}
        CO={CO}
        SO2={SO2}
        NO2={NO2}
      >
        <Chart
          PM10={chartPM10}
          PM2={chartPM25}
          SO2={chartSO2}
          NO2={chartNO2}
          CO={chartCO}
          O3={chartO3}
          Time={chartTime}
          SITENAME={chartSITENAME}
          SiteName={SiteName}
        />
      </Main>
      <Footer />
    </React.Fragment>
  );
}

export default Home;
